.hamburger {
  display: inline-block;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  margin: 0;
  outline: none;
  position: absolute;
  right: 0%;
  top: 7%;
  z-index: 4;
  transition: -webkit-transform 0.3s 0.1s ease-in-out;
  transition: transform 0.3s 0.1s ease-in-out;
  transition: transform 0.3s 0.1s ease-in-out, -webkit-transform 0.3s 0.1s ease-in-out;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  .hamburger {
    top: 6.9%;
  }
}
@media all and (min-width: 580px) and (max-width: 1200px) {
  .hamburger {
    top: 7.4%;
  }
}
@media all and (min-width: 1201px) {
  .hamburger {
    display: none;
  }
}

.hamburger--active {
  z-index: 5;
}

.hamburger__box {
  width: 3.6rem;
  height: 2.4rem;
  display: inline-block;
  position: relative;
}

.hamburger__inner {
  width: 100%;
  height: 0.2rem;
  background-color: #212121;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: background-color 0.1s 0.2s ease-in-out;
}

.hamburger__inner::before,
.hamburger__inner::after {
  width: 100%;
  height: 0.2rem;
  background-color: #212121;
  position: absolute;
  content: "";
  left: 0;
  transition: -webkit-transform 0.2s 0.2s ease-in-out;
  transition: transform 0.2s 0.2s ease-in-out;
  transition: transform 0.2s 0.2s ease-in-out, -webkit-transform 0.2s 0.2s ease-in-out;
}

.hamburger__inner::before {
  top: -1rem;
}

.hamburger__inner::after {
  top: 1rem;
}

.hamburger--active .hamburger__inner {
  background-color: transparent;
}

.hamburger--active .hamburger__inner:before {
  -webkit-transform: translateY(1rem) rotate(45deg);
          transform: translateY(1rem) rotate(45deg);
}

.hamburger--active .hamburger__inner:after {
  -webkit-transform: translateY(-1rem) rotate(-45deg);
          transform: translateY(-1rem) rotate(-45deg);
}

